html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::after,
*::before {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Space Mono", monospace;
}

h1,
h2,
h3 {
  margin-top: none;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

.input {
  padding: 0.3125rem;
}
.input__icon {
  width: 1.25rem;
}
.input__input {
  background-color: #26c0ab;
}